import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { StudentComponent } from './student/student.component';
import { HomeComponent } from './home/home.component';
import { CSEEComponent } from './csee/csee.component';
import { SignInComponent } from './student/sign-in/sign-in.component';
import { SignUpComponent } from './student/sign-up/sign-up.component';
import { MyPageComponent } from './student/my-page/my-page.component';
import { UserInformationComponent } from './student/sign-up/user-information/user-information.component';
import { SignUpCompleteComponent } from './student/sign-up/sign-up-complete/sign-up-complete.component';
import { FormsModule } from '@angular/forms';
import { HwRegisterComponent } from './csee/hw-register/hw-register.component';
import { TestModule } from './test/test.module';

export const ROUTES:Routes = [ 
    { path: '', component: HomeComponent }, 
    { 
        path: 'student', 
        component: StudentComponent, 
        children: [ 
            { path: 'signin', component: SignInComponent }, 
            { 
                path: 'signup', 
                component: SignUpComponent, 
                children: [ 
                    {path: 'user-info', component: UserInformationComponent}, 
                    {path: 'signin-complete', component: SignUpCompleteComponent} 
                ] 
            }, 
            { path: 'mypage', component: MyPageComponent } 
        ] 
    }, 
    { 
      path: 'CSEE', 
      component: CSEEComponent, 
      children: [
          { path: 'hw-register', component: HwRegisterComponent },
          ]    
    }

] 
     
 
@NgModule({
  imports: [
    RouterModule.forRoot(ROUTES),
    FormsModule,    
    HttpClientModule,
    TestModule,
  ],
  declarations: [
    HomeComponent,
    StudentComponent,
    CSEEComponent,
    SignInComponent,
    SignUpComponent,
    MyPageComponent,
    UserInformationComponent,
    SignUpCompleteComponent,
    HwRegisterComponent,
  ]
})
export class ViewModule { }
