import { Component, OnInit } from '@angular/core';
import { UserService } from '../user.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-csee',
  templateUrl: './csee.component.html',
  styleUrls: ['./csee.component.css'],
  providers: [UserService]
})

export class CSEEComponent implements OnInit {
  signin;

  constructor(private userService:UserService, private router: Router) { }

  ngOnInit() {
    this.signin = {
      username: '',
      password: '',
      email: ''
    };
  }
  SignIn(){
    this.userService.signinNewUser(this.signin).subscribe(
      response => {
        this.router.navigateByUrl('/CSEE/hw-register');
      },
      error => console.log('error', error)
    )
  }
}
